'use client';

import { Canvas, useFrame } from '@react-three/fiber';
import { Points, PointMaterial, Float } from '@react-three/drei';
import { useRef, useState } from 'react';
import * as THREE from 'three';

function StarField() {
    const ref = useRef<THREE.Points>(null!);
    const [sphere] = useState(() => {
        const points = new Float32Array(5000 * 3);
        for (let i = 0; i < 5000; i++) {
            const r = 10;
            const theta = 2 * Math.PI * Math.random();
            const phi = Math.acos(2 * Math.random() - 1);
            points[i * 3] = r * Math.sin(phi) * Math.cos(theta);
            points[i * 3 + 1] = r * Math.sin(phi) * Math.sin(theta);
            points[i * 3 + 2] = r * Math.cos(phi);
        }
        return points;
    });

    useFrame((state, delta) => {
        ref.current.rotation.x -= delta / 10;
        ref.current.rotation.y -= delta / 15;
    });

    return (
        <group rotation={[0, 0, Math.PI / 4]}>
            <Points ref={ref} positions={sphere} stride={3} frustumCulled={false}>
                <PointMaterial
                    transparent
                    color="#38bdf8"
                    size={0.05}
                    sizeAttenuation={true}
                    depthWrite={false}
                />
            </Points>
        </group>
    );
}

function FloatingShapes() {
    return (
        <>
            <Float speed={4} rotationIntensity={1} floatIntensity={2}>
                <mesh position={[-4, 2, -2]}>
                    <octahedronGeometry args={[1, 0]} />
                    <meshStandardMaterial color="#818cf8" wireframe />
                </mesh>
            </Float>
            <Float speed={5} rotationIntensity={2} floatIntensity={1}>
                <mesh position={[4, -2, -3]}>
                    <torusKnotGeometry args={[0.5, 0.2, 128, 16]} />
                    <meshStandardMaterial color="#f472b6" wireframe />
                </mesh>
            </Float>
        </>
    );
}

export default function SceneLogin() {
    return (
        <div style={{ position: 'absolute', top: 0, left: 0, width: '100%', height: '100%', zIndex: -1, background: '#020617' }}>
            <Canvas camera={{ position: [0, 0, 5], fov: 75 }}>
                <ambientLight intensity={0.5} />
                <pointLight position={[10, 10, 10]} intensity={1} />
                <StarField />
                <FloatingShapes />
            </Canvas>
        </div>
    );
}
