'use client';

import { useState, useEffect } from 'react';
import { useRouter } from 'next/navigation';
import { motion, AnimatePresence } from 'framer-motion';
import styles from './page.module.scss';
import SceneLogin from '../../components/SceneLogin';
import TiltCard from '../../components/TiltCard';

export default function LoginPage() {
    const [password, setPassword] = useState('');
    const [error, setError] = useState(false);
    const [isLoading, setIsLoading] = useState(false);
    const router = useRouter();

    useEffect(() => {
        const sessionAuth = sessionStorage.getItem('is_authenticated');
        if (sessionAuth === 'true') {
            router.push('/');
        }
    }, [router]);

    const handleLogin = async (e: React.FormEvent) => {
        e.preventDefault();
        setIsLoading(true);

        // Add a small delay for dramatic effect
        setTimeout(() => {
            if (password === 'louis_bewerbung') {
                sessionStorage.setItem('is_authenticated', 'true');
                setError(false);
                router.push('/');
            } else {
                setError(true);
                setPassword('');
                setIsLoading(false);
            }
        }, 800);
    };

    return (
        <div className={styles.loginPage}>
            <SceneLogin />

            <AnimatePresence>
                <motion.div
                    initial={{ opacity: 0, scale: 0.9, y: 20 }}
                    animate={{ opacity: 1, scale: 1, y: 0 }}
                    transition={{ duration: 0.8, ease: "easeOut" }}
                    className={styles.loginContainer}
                >
                    <TiltCard intensity={5} className={styles.tiltWrapper}>
                        <div className={styles.loginCard}>
                            <div className={styles.logoBadge}>LC</div>
                            <h1 className={styles.title}>Secure Access</h1>
                            <p className={styles.subtitle}>Portfolio von Louis Christ</p>

                            <form onSubmit={handleLogin} className={styles.form}>
                                <div className={styles.inputGroup}>
                                    <input
                                        type="password"
                                        placeholder="Zugangscode eingeben"
                                        value={password}
                                        onChange={(e) => setPassword(e.target.value)}
                                        className={error ? styles.inputError : ''}
                                        disabled={isLoading}
                                    />
                                    <div className={styles.inputGlow}></div>
                                </div>

                                {error && (
                                    <motion.span
                                        initial={{ opacity: 0, x: -10 }}
                                        animate={{ opacity: 1, x: 0 }}
                                        className={styles.errorMessage}
                                    >
                                        Zugriff verweigert. Bitte erneut versuchen.
                                    </motion.span>
                                )}

                                <button
                                    type="submit"
                                    className={`${styles.loginButton} ${isLoading ? styles.loading : ''}`}
                                    disabled={isLoading}
                                >
                                    {isLoading ? 'Verifiziere...' : 'Eintreten'}
                                </button>
                            </form>

                            <div className={styles.footer}>
                                <span>Gesteuertes Portfolio-System v2.0</span>
                            </div>
                        </div>
                    </TiltCard>
                </motion.div>
            </AnimatePresence>
        </div>
    );
}
